.gallery {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 4;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.gallery img {
  width: 95vw;
  height: auto;
}

.closeButton {
  position: absolute;
  top: 10rem;
  right: 1rem;
}

.nextButton {
  position: absolute;
  z-index: 5;
  right: 1rem;
}

.prevButton {
  position: absolute;
  z-index: 5;
  left: 1rem;
}

@media only screen and (min-width: 768px) {
  .gallery img {
    width: auto;
    height: 80vh;
  }
  .closeButton {
    top: 4rem;
    right: 2rem;
  }
  .nextButton,
  .prevButton {
    position: relative;
  }
}
